<template>
  <div class="home">
    <div class="container mt-4">
      <div class="row">
        <!-- right nav -->

        <v-card class="col-md-12 pa-4 ">
          <div>
            <!-- <h3 class="col-md-12">
              <i class="mdi mdi-pencil"></i>
              تعديل : {{ title }}
            </h3> -->
            <!-- <v-divider></v-divider> -->
            <div>
              <div>
                <v-col cols="12" sm="12">
                  <v-text-field
                    v-model="fullname"
                    label="تحديت الإسم"
                    outlined
                  ></v-text-field>
                </v-col>
                <v-col cols="12" sm="12">
                  <v-text-field
                    v-model="username"
                    label="إسم المستخدم"
                    outlined
                    class="red"
                    readonly
                  ></v-text-field>
                </v-col>
                <v-col cols="12" sm="12">
                  <v-text-field
                    v-model="id"
                    label="id"
                    readonly
                    outlined
                    class="red"
                  ></v-text-field>
                </v-col>
                <v-col cols="12" sm="12">
                  <v-text-field
                    v-model="token"
                    label="token"
                    readonly
                    class="red"
                    outlined
                  ></v-text-field>
                </v-col>
                <div class="text-center">
                  <v-btn
                    class=""
                    :loading="showProg"
                    color="success"
                    @click="updatePost()"
                  >
                    <i class="mdi mdi-pencil"></i> تحديت
                  </v-btn>
                </div>
              </div>
            </div>
            <v-divider class="ma-4"></v-divider>
          </div>
        </v-card>
        <!-- nav left -->
        <!-- <NavLeft /> -->
      </div>
    </div>
  </div>
</template>

<script>
// import NavLeft from "@/components/pages/navleft";
export default {
  name: "Home",
  components: {
    // NavLeft,
  },
  data() {
    return {
      fullname: this.$store.getters.getFullName,
      username: this.$store.getters.getUserName,
      id: this.$store.getters.getUserId,
      token: this.$store.getters.getToken,
      showProg: false,
    };
  },
  methods: {
    tryagain() {
      this.showProg = false;
    },
    updatePost: function() {
      this.showProg = true;
      let formData = new FormData();
      formData.append("fullname", this.fullname);
      formData.append("id", this.$store.getters.getUserId);
      const config = {
        headers: {
          Accept: "application/json,*/*",
          "Content-Type": "application/json",
          "Access-Control-Allow-Origin": "*",
          "Access-Control-Allow-Headers": "Authorization",
          Authorization: "Bearer " + this.$store.getters.getToken,
        },
      };
      this.$http
        .put("/users/update", formData, config)
        .then((res) => {
          this.showProg = false;
          this.$swal.fire("success", "تم التحديت بنجاح", "success");
          return res;
        })
        .catch((err) => {
          this.showProg = false;
          alert(err.message);
        });
    },
  },
};
</script>
